<template>
  <b-list-group-item style='border: none'>
    <h6 class='mb-1'>
      {{data.jobTitle}}, {{data.companyName}}
    </h6>
    <h6 class='mb-1'>
      {{data.startDate}} - {{data.endDate}}
    </h6>
    <div class='d-flex w-100, justify-content-between'>
      <small class='text-muted'>
        <b-link @click="revealEditModal(data.jobID)">Edit</b-link>
      </small>
      <small class='text-muted'>
        <b-link @click="revealRemoveModal(data.jobID)">Remove</b-link>
      </small>
      <b-modal :id="'modal-job-edit-' + data.jobID" hide-footer>
        <b-form>
          <JobForm :data="data"></JobForm>
        </b-form>
      </b-modal>
      <b-modal :id="'modal-job-remove-' + data.jobID" hide-footer>
        <b-form @submit="$emit('delete-row')">
          <b-button type="submit" variant="danger">Are you sure?</b-button>
        </b-form>
      </b-modal>
    </div>
  </b-list-group-item>
</template>

<script>
/* eslint-disable */

import Vue from 'vue';
import { FormPlugin, LinkPlugin, ModalPlugin, ListGroupPlugin, ButtonPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

const JobForm = () => import('../form/JobForm');

Vue.use(ListGroupPlugin);
Vue.use(ModalPlugin);
Vue.use(ButtonPlugin);
Vue.use(LinkPlugin);
Vue.use(FormPlugin);

export default {
  name: 'WorkExperience',
  props: [
    'data'
  ],
  components: {
    JobForm
  },
  methods:{
    async revealEditModal(_id){
      this.$bvModal.show('modal-job-edit-' + _id);
    },
    async revealRemoveModal(_id){
      this.$bvModal.show('modal-job-remove-' + _id);
    }
  }
}
</script>



